

export default async function insertionSort(n, r) {

    for (let i = 1; i < n.length; i++) {
        var j = i
        while (j > 0 && n[j] < n[j - 1]) {
            var t1 = n[j]


            r[j].current.changeColor(40)
            r[j].current.onVisited(n[j - 1])
            n[j] = n[j - 1]


            r[j - 1].current.changeColor(80)
            r[j - 1].current.onVisited(t1)
            n[j - 1] = t1

            j -= 1
            await sleep(10)
        }
    }

}

function sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}