import React, { Component } from 'react';
import './App.css';
import node from './components/node';
import Node from './components/node'
import bubbleSort from './sorting-algos/bubble-sort';
import insertionSort from './sorting-algos/insertion-sort';
import quickSort from './sorting-algos/quickSort';

import Slider from '@material-ui/core/Slider'

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      node: [],
      compare: false
    }
    this.mainData = [];
    this.refs = [];
    this.nodeSize = 40;

    this.bsClicked = this.bsClicked.bind(this);
    this.isClicked = this.isClicked.bind(this);
    this.qsClicked = this.qsClicked.bind(this);
    this.generate = this.generate.bind(this);
    this.compareAll = this.compareAll.bind(this);
    this.generateNodes = this.generateNodes.bind(this);
  }


  generate() {
    this.mainData = []
    for (let i = 0; i < this.nodeSize; i++) {
      var d = Math.random() * 100
      this.mainData.push(Math.round(d))
    }

    const nodes = []
    const r = []
    this.mainData.map((e, index) => {
      var ref = React.createRef();
      var e = <Node
        ref={ref}
        key={index}
        height={e}
        name="akash"
      />
      nodes.push(e)
      r.push(ref);
    })
    this.refs = r
    this.setState({
      node: []
    }, () => {
      this.setState({ node: nodes })
    })
  }

  componentDidMount() {
    this.generate()
  }


  bsClicked() {
    bubbleSort(this.mainData, this.refs);
  }

  isClicked() {
    insertionSort(this.mainData, this.refs);
  }

  qsClicked() {
    quickSort(this.mainData, this.refs, 0, this.mainData.length - 1);
  }


  compareAll() {
    this.setState({
      compare: true
    })
  }

  generateNodes() {
    [0, 1].map((e, index) => {

    })
  }



  render() {
    const { node } = this.state;
    return (
      <div className="App" >
        <div className="upper" >
          <h1><span style={{ color: 'purple' }}>Sorting</span>Visualiser</h1>
          <div className="sort-buttons">
            <button className="sortButton" onClick={this.bsClicked}>Bubble sort: O(n*n)</button>
            <button className="sortButton" onClick={this.isClicked}>Insertion sort: O(n*n)</button>
            <button className="sortButton" onClick={this.qsClicked}>Quick sort: O(nlogn)</button>

            {/* <button className="compareButton" onClick={this.compareAll}>Compare all</button> */}
          </div>
        </div>

        <Slider
          defaultValue={40}
          step={10}
          min={10}
          style={{ width: '200px', marginTop: '20px' }}
          onChange={(e, newValue) => {
            if (newValue != this.nodeSize) {
              this.nodeSize = newValue
              this.generate()
              console.log(newValue)
            }
          }}
        />

        <div className="nodesContainer" >
          {
            [0].map((e, index) => {
              return (<div className="nodes" >
                {node.map((e, index) => {
                  return e
                })}
              </div>)
            })
          }
        </div>

        <div style={{ display: 'flex', justifyContent: 'center' }}>
          <button className="generate" onClick={this.generate}>Generate new array</button>
        </div>
      </div >
    );
  };
}

export default App;
