
export default async function bubbleSort(n, r) {
    //const n = nodes
    //const r = refs


    var l = 10

    for (let i = 0; i < n.length; i++) {
        var swapped = false;
        for (let j = 0; j < n.length; j++) {
            // update the view
            if (n[j] > n[j + 1]) {
                console.log("swapiing")
                swapped = true
                var t1 = n[j]


                r[j].current.changeColor(40)
                r[j].current.onVisited(n[j + 1])
                n[j] = n[j + 1]


                r[j + 1].current.changeColor(80)
                r[j + 1].current.onVisited(t1)
                n[j + 1] = t1

                await sleep(10)
            } else {
                if (j != n.length - 1) {
                    r[j].current.changeColor(20)
                    r[j + 1].current.changeColor(40)
                }
                await sleep(10)
            }
        }
        if (swapped == false) {
            console.log("swapped : " + swapped)
            return
        }


    }
}

function sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}
