import React, { Component } from 'react'
import './node.css';


class Node extends Component {
    constructor(props) {
        super(props)
        this.state = {
            height: props.height,
            visited: false
        }
        this.onVisited = this.onVisited.bind(this)
    }

    onVisited(h) {
        //const h = this.state.height;
        this.setState({
            height: h,
        })
    }
    changeColor(val) {
        this.setState({
            visited: true
        })
        setTimeout(() => {
            this.setState({
                visited: false
            })
        }, val)
    }


    render() {
        const { height, visited } = this.state;
        return (
            < div className={visited ? 'node-visited' : 'node'} style={{ height: height + '%' }}>
            </div >
        );
    }
}

export default Node;
